import React, { useState, useEffect } from 'react';
import Pregunta from 'Components/Pregunta';
import Formulario from 'Components/Formulario';
import Listado from 'Components/Listado';
import ControlPresupuesto from 'Components/ControlPresupuesto';


function App() {

  const [presupuesto, guardarPresupuesto] = useState(0);
  const [restante, guardarRestantes] = useState(0);
  const [mostrarPregunta, actualizarPregunta] = useState(true);
  const [gastos, guardarGastos] = useState([]);
  const [gasto, guardarGasto] = useState([]);
  const [crearGasto, guardarCrearGasto] = useState(false);
  const [aumentarPre, guardarAumento] = useState(0);

  useEffect(() => {
    if (crearGasto) {
      
      //Agrega nuevo presupuesto
      guardarGastos([
        ...gastos,
        gasto
      ])

      guardarAumento([
        aumentarPre
      ])


      //Resta oresupuesto actual
      const presupuestoRestante = restante - gasto.cantidad
      guardarRestantes(presupuestoRestante);

      //Aumenta presupuesto
      const aumentarPresupuesto = presupuesto + aumentarPre.aumento
      guardarPresupuesto(aumentarPresupuesto)
      
      guardarCrearGasto(false);
    }

  }, [gasto, crearGasto, gastos, restante, aumentarPre, presupuesto]);

  return (
    <div className="container">
      <header>
        <h1>Gasto Semanal</h1>

        <div className="contenido-principal contenido">
          {mostrarPregunta ?
            <Pregunta
              guardarPresupuesto={guardarPresupuesto}
              guardarRestantes={guardarRestantes}
              actualizarPregunta={actualizarPregunta}
            />
            :
            <div className="row">
              <div className="one-half column">
                <Formulario
                  guardarGasto={guardarGasto}
                  guardarCrearGasto={guardarCrearGasto}
                  guardarAumento={guardarAumento}
                />
              </div>
              <div className="one-half column">
                <Listado
                  gastos={gastos}
                />
                <ControlPresupuesto
                  presupuesto={presupuesto}
                  restante={restante}
                />
              </div>
            </div>
          }
        </div>
      </header>
    </div>
  );
}

export default App;
