import React, { useState } from 'react';
import Error from 'Components/Error';
import shortId from 'shortid';
import PropTypes from 'prop-types';


const Formulario = ({ guardarGasto, guardarCrearGasto, guardarAumento }) => {

    const [nombre, guardarNombre] = useState('');
    const [cantidad, guardarCantidad] = useState(0);
    const [error, guardarError] = useState(false);
    const [aumento, guardarAumentar] = useState(0);

    const agregarGasto = e => {
        e.preventDefault();

        //validar
        if (cantidad < 1 || isNaN(cantidad) || nombre.trim() === '') {
            guardarError(true);
            return;
        }
        //construir el gasto

        const gasto = {
            nombre,
            cantidad,
            id: shortId(),
        }

        //Pasar el gasto al componente principal
        guardarGasto(gasto);
        // resetear el form
        guardarError(false);
        guardarCrearGasto(true);

        guardarNombre('');
        guardarCantidad(0);
    }

    const aumentar = e => {
        e.preventDefault();

        if(aumento < 1 || isNaN(aumento)){
            guardarError(true);
            return;
        }

        const aumentarPre = {
            aumento,
        }
        guardarAumento(aumentarPre);
        console.log(aumentarPre)
        guardarError(false);
        guardarAumentar(0)

    }


    return (
        <>
            <form
                onSubmit={agregarGasto}
            >
                <h2>Agrega tus gastos aquí</h2>
                {error ? <Error mensaje="Todos los campos son requeridos." /> : null}
                <>
                    <label>Nombre Gasto</label>
                    <input
                        type="text"
                        className="u-full-width"
                        placeholder="Ej. Transporte"
                        value={nombre}
                        onChange={e => guardarNombre(e.target.value)}
                    />
                </>
                <>
                    <label>Cantidad Gasto</label>
                    <input
                        type="number"
                        className="u-full-width"
                        placeholder="Ej. 300"
                        value={cantidad}
                        onChange={e => guardarCantidad(parseInt(e.target.value))}
                    />
                </>

                <input
                    type="submit"
                    className="button-primary u-full-width"
                    value="Agregar Gastos"
                />
            </form>
            <form
                onSubmit={aumentar}
            >
                <>
                    <label>Aumentar presupuesto</label>
                    <input
                        type="number"
                        className="u-full-width"
                        placeholder="Ej. 300"
                        value={aumento}
                        onChange={e => guardarAumentar(parseInt(e.target.value))}

                    />
                </>

                <input
                    type="submit"
                    className="button-primary u-full-width"
                    value="Aumentar Presupuesto"
                />
            </form>
        </>
    )
}

Formulario.propTypes = {
    guardarGasto: PropTypes.func.isRequired,
    guardarCrearGasto: PropTypes.func.isRequired,
    guardarAumento: PropTypes.func.isRequired
}
export default Formulario;